<template>
  <page
    @on-ready="onReady"
    :headerHeightVW="headerHeightVW"
    ref="page"
  >
    <van-nav-bar slot="header" id="header" :title="title" left-arrow @click-left="onClickLeft">
      <img slot="right" @click="handleApp" src="../../assets/images/share.png" alt="icon" class="action-menu" >
    </van-nav-bar>
  </page>
</template>

<script>
import Page from '../../components/Page'
import navigate from '../../utils/navigate.js'

export default {
  components: {
    Page
  },
  data () {
    return {
      headerHeightVW:  46 / 3.75,
      title: '',
      browser: null,
      url: '',
      currentUrl: ''
    }
  },
  methods: {
    onReady (header, main) {
      this.headerHeightVW = (header.h + 1) * 100 / header.w

      this.url = navigate.getParam('url')

      this.currentUrl = this.url

      this.title = navigate.getParam('title')

      if (!this.browser) {
        this.browser = api.require('webBrowser')
      }

      this.browser.openView({
        url: this.url,
        rect: {
          x: 0,
          y: header.h,
          w: 'auto',
          h: 'auto'
        }
      }, (ret, err) => {
        switch (ret.state) {
          case 0:
            this.currentUrl = ret.url
            break;
          case 1:
            break;
          case 2:
            this.currentUrl = ret.url
            break;
          case 3:
            this.title = ret.title
            break;
          case 4:
            this.currentUrl = ret.url
            break;
          default:
            break;
        }
      })
    },

    onClickLeft () {
      if (this.browser) {
        this.browser.historyBack((ret, err) => {
          if (!ret.status) {
            navigate.back()
          }
        })
      } else {
        navigate.back()
      }
    },

    handleApp () {
      if (api.systemType == 'ios') {
        api.openApp({
          iosUrl: this.currentUrl
        })

      } else {
        api.openApp({
          androidPkg : 'android.intent.action.VIEW',
          mimeType : 'text/html',
          uri : this.currentUrl
        }, function(ret, err) {
        })
      }
    }
  }
}
</script>

<style lang="scss">
#header {
  .action-menu {
    width: 20px;
    vertical-align: middle;
    position: absolute;
    top: -35px;
    right: -5px;
  }
}
</style>
